import React, { useContext, useEffect } from "react";
import { GithubContext } from "../context/github/githubContext";
import { Link } from "react-router-dom";
import { Repos } from "../Components/Repos";

export const Profile = ({ match }) => {
  const { getUser, getRepos, loading, user, repos } = useContext(GithubContext);
  const urlName = match.params.name;

  useEffect(() => {
    getUser(urlName);
    getRepos(urlName);
  }, []);
  if (loading) {
    return <p className="text-center">Загрузка....</p>;
  }
  const {
    name,
    company,
    avatar_url,
    location,
    bio,
    blog,
    login,
    html_url,
    followers,
    public_repos,
    public_gists,
    following
  } = user;
  return (
    <React.Fragment>
      <Link to="/" className="btn btn-link">
        На главную
      </Link>
      <div className="card mb-4">
        <div className="card-body">
          <div className="row">
            <div className="col-sm-3 text-center">
              <img src={avatar_url} alt={name} style={{ width: 200 }} />
              <h1>{name}</h1>
              {location && <p>Местоположение: {location}</p>}
            </div>
            <div className="col">
              {bio && (
                <React.Fragment>
                  <h3>BIO</h3>
                  <p>{bio}</p>
                </React.Fragment>
              )}
              <a
                href={html_url}
                className="btn btn-dark mb-4"
                target="_blank"
                rel="noopener noreferrer"
              >
                Открыть профиль
              </a>
              <ul>
                {login && (
                  <li>
                    <strong>Username:</strong> {login}
                  </li>
                )}
                {company && (
                  <li>
                    <strong>Компания:</strong> {company}
                  </li>
                )}
                {blog && (
                  <li>
                    <strong>Website:</strong> {blog}
                  </li>
                )}
              </ul>
              <div className="badge badge-primary mr-4 p-2">
                Подписчики: {followers}
              </div>
              <div className="badge badge-success mr-4 p-2">
                Подписан: {following}
              </div>
              <div className="badge badge-info mr-4 p-2">
                Репозитории: {public_repos}
              </div>
              <div className="badge badge-dark p-2">Gists: {public_gists}</div>
            </div>
          </div>
        </div>
      </div>
      <h2 className="mb-4">Последние 5 репозиториев:</h2>

      <Repos repos={repos} />
    </React.Fragment>
  );
};
